import React, {useEffect, useState} from 'react';
import { DataGrid, GridColDef } from '@mui/x-data-grid';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Table,Button,Form,InputGroup} from 'react-bootstrap';
import axios from 'axios';
import MyVerticallyCenteredModal from './Modal';


const columns: GridColDef[] = [
  { field: 'id', headerName: ',', width: 150, renderCell: () => (
    <strong>
      <Button
        variant="contained"
        color="primary"
        size="small"
        style={{ marginLeft: 16 }}
      >
        
      </Button>
    </strong>
    ),
  },
  { field: 'companyName', headerName: '', width: 150 },
  { field: 'itemCode', headerName: '', width: 150 },
  { field: 'itemName', headerName: '', width: 150 },
  { field: 'itemType', headerName: '', width: 150 },
  { field: 'partNumber', headerName: 'P/N', width: 150 },
  { field: 'itemGroup', headerName: '', width: 150 },
  { field: 'standard', headerName: '', width: 150 },
  { field: 'receiptPaymentUnit', headerName: '', width: 150 },
  { field: 'purchaseUnit', headerName: '', width: 150 },
  { field: 'purchaseUnitQuantity', headerName: '', width: 150 },
  { field: 'requiredUnit', headerName: '', width: 150 },
  { field: 'requiredUnitQuantity', headerName: '', width: 150 },
  { field: 'yieldUnit', headerName: '', width: 150 },
  { field: 'yieldUnitQuantity', headerName: '', width: 150 },
  
];


function Items() {
  const [items, setItems] = useState([])

  const [companyList, setCompanyList] = useState([])

  const [companyName, setCompanyName] = useState("")
  const [itemCode, setItemCode] = useState("")
  const [itemName, setItemName] = useState("")
  const [itemType, setItemType] = useState("")
  const [itemGroup, setItemGroup] = useState("")

  const [modalShow, setModalShow] = useState(false);

  const handleCompanyName = (e) => {
    setCompanyName(e.target.value);
  };
  const handleItemCode = (e) => {
    setItemCode(e.target.value);
  };
  const handleItemName = (e) => {
    setItemName(e.target.value);
  };
  const handleItemType = (e) => {
    setItemType(e.target.value);
  };
  const handleItemGroup = (e) => {
    setItemGroup(e.target.value);
  };

  const register = () => {
    axios.post('api/items',{
      companyName : companyName,
      itemCode : itemCode,
      itemName : itemName,
      itemType : itemType,
      itemGroup : itemGroup,
    })
        .then((response) => {
          console.log("success")
        })
        .catch((error) => {
          console.log("error")
        })
  }

  useEffect(() => {
    axios.get('api/items/company')
        .then(response => setCompanyList(response.data))
        .catch((error) => {
          console.log("error")
        })
  }, [])

  useEffect(() => {
    axios.get('api/items')
        .then(response => setItems(response.data))
        .catch((error) => {
          console.log("error")
        })}, [])


  // const findDetailItems = (companyName) => {
  //   axios.get('api/items/${companyName}')
  //   .then(response => setItems(response.data))
  // }

  const findDetailItems = (companyName) => {
    axios
      .get(`api/items/${companyName}`)
      .then(response => setItems(response.data))
  }


  return (
      <div style={{
        paddingLeft: '50px',
        paddingRight: '50px',
        height: '1400px',
        backgroundColor: 'Snow'
      }}>
        <br/>
        <h3>  
          <Button variant="primary" onClick={findDetailItems} size='lg'
                  style={{position: 'absolute', right: 0, marginRight: '140px'}}>
            
          </Button>
          <Button variant="primary" onClick={register} size='lg'
                  style={{position: 'absolute', right: 0, marginRight: '60px'}}>
            
          </Button>
        </h3>
        <br/>

        <Table>
          <tbody>
          <tr>
            <td style={{width: '110px', fontSize: '23px'}}></td>
            <td style={{width: '100px'}}>
              <Form.Select onChange={handleCompanyName} value={companyName}>
                <option></option>
                {companyList.map((item) => (
                    <option value={item} key={item}>
                      {item}
                    </option>
                ))}
              </Form.Select>
            </td>
            <td style={{width: '110px', fontSize: '23px'}}></td>
            <td style={{width: '100px'}}>
              <Form.Select onChange={handleItemType} value={itemType}>
                <option></option>
                <option value=""></option>
                <option value=""></option>
              </Form.Select>
            </td>
            <td style={{width: '110px', fontSize: '23px'}}></td>
            <td style={{width: '100px'}}>
              <Form.Select onChange={handleItemGroup} value={itemGroup}>
                <option></option>
                <option value=""></option>
                <option value=""></option>
              </Form.Select>
            </td>
            <td style={{width: '110px', fontSize: '23px'}}></td>
            <td style={{width: '200px'}}>
              <InputGroup className="mb-3">
                <Form.Control
                    placeholder="CODE"
                    aria-label="CODE"
                />
                <Button variant="primary" size='sm' onClick={() => setModalShow(true)}>
                  
                </Button>

                <MyVerticallyCenteredModal
                    show={modalShow}
                    onHide={() => setModalShow(false)}
                />

                <Form.Control
                    placeholder="NAME"
                    aria-label="NAME"
                />
              </InputGroup>
            </td>
            <td></td>
          </tr>
          </tbody>
        </Table>
        <Table>
          <tbody>
          <tr>
            <td style={{width: '110px', fontSize: '23px'}}></td>
            <td style={{width: '100px'}}>
              <Form.Control onChange={handleItemCode} value={itemCode}/>
            </td>
            <td style={{width: '110px', fontSize: '23px'}}></td>
            <td style={{width: '100px'}}>
              <Form.Control
                  onChange={handleItemName} value={itemName}
              />
            </td>
            <td style={{width: '110px', fontSize: '23px'}}></td>
            <td style={{width: '100px'}}>
              <Form.Select>
                <option value=""></option>
                <option value=""></option>
              </Form.Select>
            </td>
            <td></td>
          </tr>
          </tbody>
        </Table>
        <br/>

        <h4>  </h4>
        <div style={{height: 1000, width: '100%'}}>
          <DataGrid rows={items} columns={columns}/>
        </div>
      </div>
  );
  
  
}      

export default Items;